import React, { useEffect } from "react";
import Project from "./Project/Project";
import splash from "./splash.png";
import stab from "./project-imgs/stab.png";
import phoneShopPrototype from "./project-imgs/phoneShopPrototype.png";
import headphones from "./project-imgs/headphones.png"
import raven_hotel from "./project-imgs/raven_hotel.png"
import marilynArt from "./project-imgs/marilynArt.png"
import covid from "./project-imgs/covid.png"

import AOS from "aos";
import "aos/dist/aos.css";

export default function Projects() {
  useEffect(() => {
    AOS.init();
    AOS.refresh();
  });
  return (
    <div className="Projects">
      <div
        data-aos="zoom-in"
        className="projectTite-container"
        id="projectTitle"
      >
        <img src={splash} alt="paint-spash" />
        <h1 className="Projects-title">Projects</h1>
      </div>

      <div id="projects"></div>

      <div className="project-container">
      <Project
          img={covid}
          title={"Covid Tracker"}
          desc={
            ""
          }
          demoLink="https://szathrobi.github.io/covid-tracker/"
          codeLink="https://github.com/SzathRobi/covid-tracker/tree/master"
          aosEffect="zoom-in-right"
        />
        <Project
          img={headphones}
          title={"Headphones Landing Page"}
          desc={
            "A nice and appealing landing page to promote a single product"
          }
          demoLink="https://szathrobi.github.io/Headset-Promotion/"
          codeLink="https://github.com/SzathRobi/Headset-Promotion/tree/master"
          aosEffect="zoom-in-up"
        />
        <Project
          img={stab}
          title={"Stabofficial"}
          desc={
            "I built this page for my brother's friends who just started their own filming business. They were very happy about their new web page!"
          }
          demoLink="http://www.stabofficial.hu/"
          codeLink="https://github.com/SzathRobi/stabofficial"
          aosEffect="zoom-in-left"
        />
        <Project
          img={raven_hotel}
          title={"Raven Hotel"}
          desc={
            "A multipage hotel website template with React and Material-UI."
          }
          demoLink="https://szathrobi.github.io/raven-hotel/"
          codeLink="https://github.com/SzathRobi/raven-hotel/tree/master"
          aosEffect="zoom-in-right"
        />
        <Project
          img={phoneShopPrototype}
          title={"Phone-Shop-Prototype"}
          desc={"Phone shop with React, and React-Router and Material-UI"}
          demoLink="https://szathrobi.github.io/Phone-Shop-Prototype/"
          codeLink="https://github.com/SzathRobi/Phone-Shop-Prototype/tree/master"
          aosEffect="zoom-in-up"
        />

        <Project
          img={marilynArt}
          title={"Marilyn Art"}
          desc={
            "Webshop frontend for minerals and accessories"
          }
          demoLink="https://szathrobi.github.io/MarilynArt/"
          codeLink="https://github.com/SzathRobi/MarilynArt/tree/master"
          aosEffect="zoom-in-left"
        />
      </div>
    </div>
  );
}
